export default {
	data() {
		return {
			$app: this.$app,
			fid: 0,
			userRank: [],
			page: 1,
			leader_uid: 0,
		};
	},
	onLoad(option) {
		this.fid = option.fid
		this.loadData()
	},
	onReachBottom() {
		this.page++
		this.loadData()
	},
	methods: {
		exit(uid) {
			this.$app.modal(`是否退出该粉丝团`, () => {
				this.$app.request('fans/exit', {
					user_id: uid,
				}, res => {
					this.$app.toast(`退出成功`)
					setTimeout(() => {
						// this.$app.goPage('/pages/fans/fans_list')

						uni.navigateBack({
							delta: 2
						})
					}, 1000)
				}, 'POST', true)
			})

		},
		loadData() {
			this.$app.request('fans/member', {
				fid: this.fid,
				page: this.page,
			}, res => {
				this.leader_uid = res.data.leader_uid
				if (this.page == 1) {
					this.userRank = res.data.list
				} else {
					this.userRank = this.userRank.concat(res.data.list)
				}
			})
		},
	}
}
